.main-menu {
  position: fixed;
  top: 5.5rem;
  left: 0;
  z-index: -1;
  display: none;
  overflow: hidden;
  overflow-y: auto;
  width: 100%;
  height: stretch;
  background-color: #ffffff;
}
.main-menu.animated {
  animation-duration: 0.6s;
}
.main-menu__list {
  list-style: none;
  padding: 3rem 18vw;
  padding-bottom: 6rem;
}
.main-menu__list .main-menu__additional {
  display: none;
}
.main-menu__list-item {
  margin-bottom: 2.5rem;
  font-size: 1.6rem;
}
.main-menu__list-item.dropdown:focus {
  outline: none;
}

@media (min-width: 768px) {
  .main-menu {
    width: auto;
    height: auto;
    position: static;
    overflow: visible;
    display: block;
    border-top: none;
  }
  .main-menu__list {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 0;
    margin-left: 1rem;
  }
  .main-menu__list > * {
    display: inline-block;
    vertical-align: middle;
  }
  .main-menu__list-item {
    margin-right: 3vw;
    margin-bottom: 0;
    font-size: 1.4rem;
  }
  .main-menu__list-item.dropdown:focus-within {
    outline: none;
  }
  .main-menu__list-item.dropdown:focus-within .dropdown__body {
    display: block;
  }
}
@media (min-width: 768px) and (max-width: 890px) {
  .main-menu__list-item_hidden {
    display: none;
  }
}
@media (min-width: 768px) and (max-width: 890px) {
  .main-menu .main-menu__additional {
    display: inline-block;
  }
}
@media (min-width: 768px) {
  .main-menu .dropdown__trigger::before {
    top: 0.2em;
  }
}
@media (min-width: 768px) {
  .main-menu__submenu {
    top: 4.5rem;
    left: auto;
    width: calc(100vw - 4.2rem);
    transform: none;
  }
}
@media (min-width: 768px) {
  .main-menu__hidden-list {
    margin: 0;
    padding: 0;
    list-style: none;
    top: 2.5rem;
    min-width: 12rem;
    padding: 1rem 2rem;
    text-align: center;
  }
  .main-menu__hidden-list::before {
    width: 100%;
    height: 0.5rem;
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
  }
}
@media (min-width: 768px) {
  .main-menu__hidden-item {
    margin-bottom: 0.8rem;
  }
  .main-menu__hidden-item:last-of-type {
    margin-bottom: 0;
  }
}
@media (min-width: 1024px) {
  .main-menu {
    background: none;
  }
  .main-menu__list {
    margin-left: 0;
  }
  .main-menu__list-item {
    margin: 0 0.8vw;
    font-size: 1.6rem;
  }
  .main-menu__list-item.dropdown {
    position: static;
  }
  .main-menu__submenu {
    top: auto;
    right: 1vw;
    left: 1vw;
    width: auto;
    margin-top: 2.5rem;
    transform: none;
  }
}
@media (min-width: 1200px) {
  .main-menu__list-item {
    font-size: 1.5rem;
  }
}
@media (min-width: 1400px) {
  .main-menu__list-item {
    margin-right: 1rem;
    margin-left: 1rem;
    font-size: 1.6rem;
  }
}
