    'header-image-1': $theme-9-header-image-1,
    'header-image-2': $theme-9-header-image-2
  ),
  '10': (
    'primary': hsl($theme-10-hue, $theme-10-saturation, $theme-10-lightness),
    'primary-transparent': hsla($theme-10-hue, $theme-10-saturation, $theme-10-lightness, 0.7),
    'primary-x-transparent': hsla($theme-10-hue, $theme-10-saturation, $theme-10-lightness, 0.3),
    'secondary': hsl(0, 78%, 72%),
    'secondary-transparent': hsla(0, 78%, 72%, 0.7),
    'secondary-x-transparent': hsla(0, 78%, 72%, 0.3),
    'background': $theme-10-background,
    'header-image-1': $theme-10-header-image-1
  ),
  '11': (
    'primary': hsl($theme-11-hue, $theme-11-saturation, $theme-11-lightness),
    'primary-transparent': hsla($theme-11-hue, $theme-11-saturation, $theme-11-lightness, 0.7),
    'primary-x-transparent': hsla($theme-11-hue, $theme-11-saturation, $theme-11-lightness, 0.3),
    'secondary': hsl(0, 78%, 72%),
    'secondary-transparent': hsla(0, 78%, 72%, 0.7),
    'secondary-x-transparent': hsla(0, 78%, 72%, 0.3),
    'background': $theme-11-background,
