    'background': $theme-main-background,
    'header-image-1': $theme-main-header-image-1,
    'header-image-2': $theme-main-header-image-2
  )
) !default;

// Инициализируем наши переменные как `null`, таким образом,
// при использовании вне `themify`, они ничего не выведут.
$color-primary: null;
$color-primary-transparent: null;
$color-primary-x-transparent: null;
$color-secondary: null;
$color-secondary-transparent: null;
$color-secondary-x-transparent: null;
$color-background: null;
$header-image-1: null;
$header-image-2: null;

/// Миксин Themify
/// @author Hugo Giraudel
/// @param {Map} $themes [$themes] - ассоциативный массив с темами
@mixin themify($themes: $themes) {
  // Проход по темам
  @each $theme, $colors in $themes {
    // Создание селектора (типа `.theme-main .class`)
    .theme-#{$theme} & {
      // Задание переменных темы как глобальных с `!global`
      $color-primary: map-get($colors, 'primary') !global;
      $color-primary-transparent: map-get($colors, 'primary-transparent') !global;
      $color-primary-x-transparent: map-get($colors, 'primary-x-transparent') !global;
      $color-secondary: map-get($colors, 'secondary') !global;
      $color-secondary-transparent: map-get($colors, 'secondary-transparent') !global;
      $color-secondary-x-transparent: map-get($colors, 'secondary-x-transparent') !global;
      $color-background: map-get($colors, 'background') !global;
      $header-image-1: map-get($colors, 'header-image-1') !global;
      $header-image-2: map-get($colors, 'header-image-2') !global;

      // Вывод пользовательского контента
      @content;

      // Глобальное отключение переменных темы с `!global`
      $color-primary: null !global;
      $color-primary-transparent: null !global;
      $color-primary-x-transparent: null !global;
      $color-secondary: null !global;
      $color-secondary-transparent: null !global;
      $color-secondary-x-transparent: null !global;
      $color-background: null !global;
    }
  }
}
